.b-callback-form {
  padding: rem(92px) 0 rem(72px) 0;
  background: #202931;
}
.b-callback-form__hidden {

}
.b-callback-form__left {

}
.b-callback-form__title {
  @include footerMain();
  @include margin-bottom-not-last(rem(34px));
  color: #FFFFFF;
}
.b-callback-form__description {
  @include footerDescription();
  color: rgba(255, 255, 255, 0.6);
  max-width: 340px;
  @include margin-bottom-not-last(rem(34px));
}
.b-callback-form__link-list {
  display: flex;
  align-items: center;
}
.b-callback-form__link {
  width: 34px;
  height: 34px;
  border-radius: 100%;
  border: 1px solid #BAA174;
  display: flex;
  align-items: center;
  justify-content: center;
  fill: #BAA174;
  transition: 0.3s fill, 0.3s background;
  &:hover {
    fill: #202931;
    background: #BAA174;
  }
  svg {
    height: 15px;
  }
  &:first-child {
    margin-right: 17px;
  }
}
.b-callback-form__right {

}
.b-callback-form__form {

}
.b-callback-form__form-control {
  position: relative;
  display: block;
  background: transparent;
  border: none;
  border-bottom: 1px solid #645C52;
  width: 100%;
  padding: rem(20px) 0;
  &::placeholder {
    @include cardDescription();
    color: #FFFFFF;
  }
  @include cardDescription();
  color: #FFFFFF;
}
.b-callback-form__form-description {
  font-family: $Helvetica;
  font-style: normal;
  font-weight: 300;
  font-size: 12px;
  line-height: 1.2;
  letter-spacing: 0.04em;
  color: #959595;
  margin-bottom: rem(50px);
}
.b-callback-form__submit-block {
  display: flex;
  align-items: center;
  cursor: pointer;
  transition: 0.3s padding-left;
  &:hover {
    padding-left: g(8);
  }
}
.b-callback-form__arrow {
  svg {
    fill: #BAA174;
    width: 53px;
    height: 30px;
  }

  margin-right: 22px;
}
.b-callback-form__btn {
  font-family: $Helvetica;
  font-style: normal;
  font-weight: 300;
  font-size: rem(30px);
  line-height: 1;
  letter-spacing: 0.04em;
  color: #BAA174;
  background: transparent;
  border: none;
}
.b-callback-form__label_required {
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  &:before {
    position: absolute;
    content: "*";
    right: 0;
    top: 50%;
    transform: translateY(-50%);
    width: 10px;
    height: 10px;
    background: transparent;
    font-family: $Helvetica;
    font-style: normal;
    font-weight: 300;
    font-size: rem(25px);
    line-height: 1;
    letter-spacing: 0.04em;
    color: #959595;
  }
}
.b-callback-form__form-group {
  position: relative;
  @include margin-bottom-not-last(10px);
  &:nth-child(3) {
    @include margin-bottom-not-last(15px);
  }
}
@include media-breakpoint-down(md) {
  .b-callback-form__link-list {
    display: none;
  }
}
@include media-breakpoint-down(sm) {
  .b-callback-form__form {
    display: flex;
    flex-direction: column;
  }
  .b-callback-form__form-description {
    order: 2;
    margin-bottom: 0;
    text-align: center;
    padding: 0 40px;
  }
  .b-callback-form__description {
    max-width: 250px;
  }
  .b-callback-form__form-control {
    &:nth-child(3) {
      @include margin-bottom-not-last(30px);
    }
  }
  .b-callback-form__submit-block {
    margin-bottom: 30px;
  }

}
.b-callback-form__content {
  display: flex;
  justify-content: space-between;
  padding: 0 g(12);
  @include media-breakpoint-down(lg) {
    flex-direction: column;
  }
  @include media-breakpoint-down(md) {
    padding: 0;
  }
}
.b-callback-form_modal {
  padding: g(10);

  .b-callback-form__content {
    padding: 0;
    flex-direction: column;
  }
  .b-callback-form__description {
    display: none;
  }
  .b-callback-form__link-list {
    display: none;
  }
  .b-callback-form__title {
    font-size: rem(55px);
  }
}

