  color: #8b8b8b;
  font-size: 12px;
  font-weight: 400;
  line-height: 1.08;
}

.checkbox a {
  color: #CBA470;
}

.checkbox input {
  display: none;
}

.checkbox > span {
  padding-left: 1.375rem;
  position: relative;
  display: inline-block;
}

.checkbox > span:before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  display: inline-block;
  border: 1px solid #D3D2D2;
  background-color: #fff;
  width: 1.0625rem;
  height: 1.0625rem;
  transition: 0.3s;
  margin-top: -0.3em;
  border-radius: 3px;
}

.checkbox > span:after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  display: inline-block;
  background-color: #ececec;
  width: 1.0625rem;
  height: 1.0625rem;
  opacity: 0;
  transition: 0.3s;
  margin-top: -0.3em;
}

.checkbox input:checked + span:before {
  content: '';
}

.checkbox input:checked + span:after {
  content: '';
  background: #CBA470;
  transform: scale(0.5);
  opacity: 1;
}

@media (max-width: 991.98px) {
  .checkbox {
    min-height: 30px;
    display: flex;
    align-items: center;
  }
  .checkbox > span {
    padding-left: 50px !important;
  }
  .checkbox > span:before,
  .checkbox > span:after {
    width: 30px;
    height: 30px;
    margin-top: 0;
    top: 50%;
    transform: translateY(-50%);
  }
  .checkbox input:checked + span:after {
    transform: translateY(-50%) scale(0.5);
  }
}

