.b-main-slider {
  @include min-height-in-vh(100);
  display: flex;
  flex-direction: column;
  background: #202931;
  .swiper-slide {
    overflow: hidden;
  }
  .swiper-container {
    width: 100%;
  }
}
.b-main-slider__slider {
  flex-grow: 1;
  display: flex;
}
.b-main-slider__title {
  @include sectionTitle();
  color: #FFFFFF;
  @include margin-bottom-not-last(rem(66px));
}
.b-main-slider__top {
  position: relative;
}
.b-main-slider__bottom {
  display: flex;
  align-items: center;
}
.b-main-slider__slide {
  position: relative;
  width: 100%;
  height: 100%;
  background: #202931;

}
.b-main-slider__container  {
  height: 100%;
}
.b-main-slider__row {
  height: 100%;
  @include gutter(rem(50px));
}
.b-main-slider__about {

}
.b-main-slider__descr {
  font-size: rem(22);
  color: #222222;
  margin-bottom: g(10);
}
.marker {
  color: #222222;
}
.b-main-slider__navigation {
  margin-left: auto;
}
.b-main-slider__mobile-col {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
@include media-breakpoint-down(md) {
    .b-main-slider__mobile-col {
        padding-top: g(13) !important;
        margin-bottom: g(12);
    }
}

.b-main-slider__slider-img {
  height: 100%;
  @include cover;
  @each $bp, $container-width in $container-max-widths {
    @include media-breakpoint-only($bp) {
      margin-right: calc((100vw - #{$container-width - $grid-gutter-width}) / 2 * -1);
    }
  }
}
.b-main-slider__count {

  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: 9px 20px 2px;
  background: #3880C4;
  line-height: 1;
  b {
    font-style: normal;
    font-weight: 700;
    font-size: rem(36);
    text-transform: uppercase;
    color: #fff;
    line-height: 1;
  }
}
.b-main-slider__mobile-navigation {
  display: none;
}
@include media-breakpoint-down(md) {
  .b-main-slider {
    min-height: auto;
  }
  .b-main-slider__row {
    display: flex;
    flex-direction: column-reverse;
    height: auto;
  }
  .b-main-slider__slider-img {
    height: auto;
    aspect-ratio: 16/10;
    @each $bp, $container-width in $container-max-widths {
      @include media-breakpoint-only($bp) {
        margin-left: calc((100vw - #{$container-width - $grid-gutter-width}) / 2 * -1);
      }
    }
  }
  .b-main-slider__mobile-col {
    order: 1;
  }
  .b-main-slider__title {
    display: flex;
    justify-content: center;
    @include margin-bottom-not-last(30px);
  }
  .b-main-slider__navigation {
    display: none;
  }
  .b-main-slider__bottom {
    justify-content: center;
  }
  .b-main-slider__row {
    @include gutter-y(30px);
  }
  .b-main-slider__mobile-navigation {
    display: block;
    @include swiper-buttons();
    padding: 30px 0;
  }
  .b-main-slider__mobile-navigation-block {
    display: flex;
    align-items: center;
    justify-content: center;
    .swiper-pagination-fraction {
      color: #202931;


      .swiper-pagination-current {
        font-family: $Helvetica;
        font-style: normal;
        font-weight: 400;
        font-size: 14px;
        line-height: 16px;

        letter-spacing: 0.01em;

        color: #E1C39D;
      }
    }
    .swiper-button-prev, .swiper-button-next {
      margin: 0 30px;
    }
    .swiper-button-prev {
      z-index: 11;
    }
    .swiper-pagination-total {
      display: none;
    }
  }

}
@include media-breakpoint-down(sm) {
  .b-main-slider__slider-img {
    aspect-ratio: 1;
  }
  .b-main-slider__slider-img {
    margin-left: -20px;
    margin-right: -20px;
  }
  .b-main-slider__title {
    text-align: center;
    line-height: 116.5%;
  }
}


