.b-tabs {
  .nav-pills {
    display: flex;
    justify-content: space-between;
    gap: g(6);
    margin-bottom: g(10);
    flex-wrap: nowrap;
    white-space: nowrap;
    overflow-x: auto;
  }
  .nav-link {
    @include footerDescription;
    background: #F3F3F3;
    border-radius: 2px;
    color: #626262;
    padding: g(8) g(11);
    line-height: 1;
    @include media-breakpoint-down(md) {
      padding: g(7) g(10);
    }
  }
  .nav-link.active {
    color: #FFFFFF;
    background: #BAA174;
  }
}
.b-tabs__nav {
  width: 100%;
  overflow-x: auto;
}
.b-tabs__text {

}
.b-tabs__cont {
  padding-bottom: g(10);
  border-bottom: 1px solid #D2D2D2;
}
.b-tabs__content {
  display: flex;
  gap: g(12);
  justify-content: space-between;
  padding: 0 g(8);
  @include media-breakpoint-down(md) {
    flex-direction: column;
    padding: 0;
    gap: g(10);
  }
}
.b-tabs__subtitle {
  @include cardDescription;
  font-size: 18px;
  color: #BAA174;
  margin-bottom: g(10);
}
.b-tabs__title {
  @include footerMain;
  font-size: rem(65px);
  margin-bottom: g(8);
  @include media-breakpoint-down(md) {
    margin-bottom: g(10);
  }
}
.b-tabs__desc {
  @include footerDescription;
  color: #202931;
  max-width: 440px;
}
.b-tabs__img {
  img {
    width: 100%;
  }
  @include media-breakpoint-down(sm) {
    margin-left: g(12);
    margin-right: 1px;
  }
}
.page__innert-b-tabs {

}
.b-tabs_black {
  background: #202931;
  padding: rem(70px) 0;
    .nav-link {
      background: #2C3640;
      color: #BAA174;
    }
  .b-tabs__title {
    color: #FFFFFF;
  }
  .b-tabs__desc {
    color: #FFFFFF;
  }
  .b-tabs__cont {
    padding-bottom: 0;
    border: none;
  }

}
.b-tabs_left {
  @include media-breakpoint-down(sm) {
    .b-tabs__img {
      margin-right: 0;
      margin-left: 1px;
    }
  }


}
