.b-numbers {
  --inside-size: 200px;
}

.b-numbers__grid {
  @include grid-template-columns(4);
}
.b-numbers__item {
  position: relative;
  width: rem(336px);
  height: rem(336px);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  &:after {
    content: '';
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: rem(336px);
    height: rem(336px);
    border-radius: 50%;
    border: 1px solid #E1C39D;
  }
}
.b-numbers__num {
  @include numberNum;
  @include margin-bottom-not-last(10px);
  color: #202931;
}
.b-numbers__item-title {
  @include pointTitle;
  color: #202931;
  text-align: center;
  max-width: 150px;
  @include media-breakpoint-down(sm) {
    max-width: 140px;
  }
}
@media (max-width: 1500px) {
  .b-numbers__item {
    width: var(--inside-size);
    height: var(--inside-size);
    &:after {
      width: var(--inside-size);
      height: var(--inside-size);
    }
  }
}
@include media-breakpoint-down(lg) {
  .b-numbers__item {
    width: var(--inside-size);
    height: var(--inside-size);
    &:after {
      width: var(--inside-size);
      height: var(--inside-size);
    }
  }
}
@include media-breakpoint-down(md) {
  .b-numbers__grid {
    @include grid-template-columns(2);
  }
  .b-numbers__item {
    width: var(--inside-size);
    height: var(--inside-size);
    &:after {
      width: var(--inside-size);
      height: var(--inside-size);
    }
  }
}
@include media-breakpoint-down(sm) {
  .b-numbers__grid {
    margin-left: -7.5px;
  }
  .b-numbers__item {

    width: var(--inside-size);
    height: var(--inside-size);
    &:after {
      width: var(--inside-size);
      height: var(--inside-size);
    }

    }

  }
@include media-breakpoint-down(xs) {
  .b-numbers__item {

    width: var(--inside-size);
    height: var(--inside-size);
    &:after {
      width: var(--inside-size);
      height: var(--inside-size);
    }

  }

}


