.b-tabs-slider {
  .nav-pills {
    display: flex;
    justify-content: space-between;
    gap: g(6);
    margin-bottom: g(10);
    flex-wrap: nowrap;
    white-space: nowrap;
    overflow-x: auto;
  }
  .nav-link {
    @include footerDescription;
    background: #F3F3F3;
    border-radius: 2px;
    color: #626262;
    padding: g(8) g(11);
    @include media-breakpoint-down(md) {
      padding: g(7) g(10);
    }
  }
  .nav-link.active {
    color: #FFFFFF;
    background: #BAA174;
  }
}
.b-tabs-slider__nav {
  width: 100%;
  overflow-x: auto;
}
.b-tabs-slider__text {

}
.b-tabs-slider__content {
}
.b-tabs-slider__subtitle {
  @include cardDescription;
  font-size: 18px;
  color: #BAA174;
  margin-bottom: g(10);
}
.b-tabs-slider__title {
  @include footerMain;
  font-size: rem(65px);
  margin-bottom: g(8);
  @include media-breakpoint-down(md) {
    margin-bottom: g(10);
  }
}
.b-tabs-slider__desc {
  @include footerDescription;
  color: #202931;
  max-width: 440px;
}
.b-tabs-slider__img {
  img {
    width: 100%;
  }
}
.b-tabs-slider__bottom {
  display: flex;
  @include media-breakpoint-down(md) {
    flex-direction: column;
  }
}
.b-tabs-slider__plan {
  width: 70%;
  padding: g(10);
  border-top: 1px solid #D9D9D9;
  border-bottom: 1px solid #D9D9D9;
  border-right: 1px solid #D9D9D9;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  gap: 20px;
  &:before {
    position: absolute;
    content: '';
    top: -1px;
    right: -1px;
    width: 15px;
    height: 1px;
    background: #FFFFFF;
  }
  &:after {
    position: absolute;
    content: '';
    bottom: -1px;
    right: -1px;
    width: 15px;
    height: 1px;
    background: #FFFFFF;
  }
  @include media-breakpoint-down(md) {
    width: 100%;
    border-right: none;
    border-bottom: none;
  }
}
.b-tabs-slider__info {
  padding: g(10);
  border-top: 1px solid #D9D9D9;
  border-bottom: 1px solid #D9D9D9;
  position: relative;
  &:before {
    position: absolute;
    content: '';
    top: -1px;
    left: -1px;
    width: 15px;
    height: 1px;
    background: #FFFFFF;
  }
  &:after {
    position: absolute;
    content: '';
    bottom: -1px;
    left: -1px;
    width: 15px;
    height: 1px;
    background: #FFFFFF;
  }
}
.b-tabs-slider__grid {
  @include grid-template-columns(2);
  column-gap: g(12);
  row-gap: g(10);
  margin-bottom: g(10);
}
.b-tabs-slider__g-item {

}
.b-tabs-slider__g-title {
  @include cardDescription;
  margin-bottom: g(3);
  @include media-breakpoint-down(sm) {
    font-size: 14px;
  }
}
.b-tabs-slider__g-text {
  @include cardTitle;
  @include media-breakpoint-down(sm) {
    font-size: 18px;
  }
}
.b-tabs-slider__about {
  display: flex;
  gap: g(5);
  margin-bottom: g(10);
}
.b-tabs-slider__a-item {
  padding: g(6) g(10);
  border: 1px solid #DBDBDB;
  border-radius: 2px;
  @include cardDescription;
  @include media-breakpoint-down(sm) {
    font-size: 14px;
  }
}
.b-tabs-slider__i-image {
  img {
    @include media-breakpoint-down(md) {
      width: 100%;
    }
  }
}
.b-tabs-slider__bottom-ar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 20px;

}
.b-tabs-slider__pdf {
  display: flex;
  align-items: center;
  gap: 10px;
  &:hover {
    .b-tabs-slider__arrow {
      padding-top: 10px;
    }
  }
}
.b-tabs-slider__arrow {
  display: flex;
  justify-content: center;
  align-items: center;
  transition: 0.3s padding-top;
}
.b-tabs-slider__arr-text {
  font-family: $Helvetica;
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  letter-spacing: 0.04em;
  color: #BAA174;
  cursor: pointer;
}

