.b-location {

}
.b-location__title {
  @include sectionTitle;
  color: #202931;
  margin-bottom: rem(44px);
}
.b-location__map {
  position: relative;
}
.b-location__img {
  img {
    width: 100%;
    aspect-ratio: 2.5;
    display: block;
  }
}
.b-location__point {
  position: absolute;
  width: 44px;
  height: 44px;
  border-radius: 100%;
  background: #BAA174;

  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  &:hover {
    .b-location__point-inner {
      opacity: 1;
    }
  }
}
.b-location__point-plus {
  width: 10px;
  height: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.b-location__point-inner {
  opacity: 0;
  position: absolute;
  left: 35px;
  bottom: 100%;
  width: 288px;
  aspect-ratio: 1.5;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  padding: 20px 30px;
  @include cover;
  pointer-events: none;
  transition: 0.3s opacity;
  z-index: 10;
}
.b-location__text {
  margin-top: auto;
  @include pointTitle;
  color: #FFFFFF;
}

@include media-breakpoint-down(lg) {
    .b-location__point {
      width: 30px;
      height: 30px;
    }
  .b-location__point-inner {
    width: 200px;
    padding: 10px 15px;
  }
  .b-location__text {
    font-size: 12px;
  }
}

@include media-breakpoint-down(sm) {
  .b-location__point {
    width: 15px;
    height: 15px;
    svg {
      width: 5px;
      height: 5px;
    }
  }
  .b-location__point-inner {
    width: 100px;
    padding: 5px 7px;
    left: 5px;
  }

}

