// Mixins.

 $baseFontSize : 16;
@function rem($pixels, $context: $baseFontSize) {
    @if (unitless($pixels)) {
        $pixels: $pixels * 1px;
    }

    @if (unitless($context)) {
        $context: $context * 1px;
    }

    @return $pixels / $context * 1rem;
}
@function g($n) {
    @return var(--g#{$n});
}
@mixin min-height-in-vh($n) {
    min-height: #{$n}vh;
    min-height: calc(var(--vh, 1vh) * #{$n});
}

@mixin btn {
    display: inline-block;
    line-height: 1;
    text-align: center;
    transition: 0.3s background, 0.2s color, 0.2s border;
    position: relative;
    cursor: pointer;

    font-family: $Helvetica;
    color: #FFFFFF;
    font-weight: 300;
    font-size: 14px;

    padding:  21px 31px;
    background: #BAA174;
    border-radius: 2px;

    @include media-breakpoint-down(sm) {
        width: 100%;
        padding-left: 5px;
        padding-right: 5px;
    }
    &:hover {
        background: #DBBA80;
    }
}

@mixin btn-block {
    display: block;
    width: 100%;
    padding-left: 5px;
    padding-right: 5px;
}
