@mixin zoom {
    @include media-breakpoint-up(lg) {
        $galleryTransition: 0.6s;
        position: relative;
        &:before {
            content: '';
            display: block;
            position: absolute;
            top: 0;
            right: 0;
            left: 0;
            bottom: 0;
            background: rgba(45, 33, 23, 0.3);
            z-index: 5;
            opacity: 0;
            transition: $galleryTransition;
        }
        &:after {
            content: '';
            display: block;
            position: absolute;
            top: 0;
            right: 0;
            left: 0;
            bottom: 0;
            background: url(../img/general/zoom.svg);
            background-position: center center;
            background-repeat: no-repeat;
            background-size: rem(46) rem(47);
            z-index: 10;
            display: none;
            animation: top 0.6s forwards;

        }
        &:hover {
            &:before,
            &:after {
                opacity: 1;
                display: block;
            }
        }
    }
}
@mixin link-h {
    &:hover {
        color: #C4AB54;
    }
}
@mixin cover {
    background-size: cover;
    background-position: center center;
}
@mixin modal-title {
    font-weight: 300;
    font-size: rem(36);
    color: #E5E5E5;
    text-transform: uppercase;
    margin-bottom: rem(30);
}
@mixin list {
    margin: 0;
    padding: 0;
    ul {
        margin: 0;
        padding: 0;
    }
    li {
        font-weight: 400;
        display: inline-flex;
        width: 100%;
        margin-bottom: 15px;
        position: relative;
        padding-left: rem(22);
        &:before {
            content: '';
            display: inline-block;
            width: rem(8);
            height: rem(8);
            background: #C2A84D;
            margin-right: rem(13);
            flex-shrink: 0;
            position: absolute;
            top: 0.3em;
            left: 0;
        }
    }
}
@mixin testFix() {
    @media (min-height: 1400px) {
        min-height: 800px;
        height: auto;
    }
}

@mixin gutter($n) {
    @if (unitless($n)) {
        $n: $n * 1px;
    }
    margin: 0 #{$n * -1};
    & > div {
        padding: 0 #{$n};
    }
}
@mixin text-last-mb-fix() {
    &>*:last-child {
        margin-bottom: 0;
    }
}
@mixin gutter-y($n: 30px) {
    @if (unitless($n)) {
        $n: $n * 1px;
    }
    margin-bottom: #{$n * -1};
    &>div {
        margin-bottom: $n;
    }
}
@mixin margin-bottom-not-last($n) {
    &:not(:last-child) {
        margin-bottom: $n;
    }
}

@mixin swiper-buttons() {
    .swiper-button-prev,
    .swiper-button-next {
        position: inherit;
        user-select: none;
        cursor: pointer;
        background: transparent;
        align-items: center;
        display: flex;
        justify-content: center;
        width: rem(49);
        height: rem(49);
        border-radius: 100%;
        pointer-events: visible;
        transition: 0.3s background;
        border: 1px solid #BAA174;
        fill: #BAA174;
        margin-top: 0;

    }
    .swiper-button-next {
        &:hover {
            transition: 0.3s transform;
            transform: scale(1.3);
        }
    }
    .swiper-button-prev {
        transform: rotate(180deg);
        margin-right: rem(22px);
        &:hover {
            transition: 0.3s transform;
            transform: scale(1.3) rotate(180deg);
        }
    }
}
@mixin grid-template-columns($n) {
    display: grid;
    grid-template-columns: repeat($n, minmax(0, 1fr));
}

@mixin col-span($n) {
    grid-column: auto / span $n;
}

@mixin row-span($n) {
    grid-row: auto / span $n;
}


@mixin swiper-arrow() {
    display: flex;
    pointer-events: none;
}

