.b-plans-slider {
  padding: g(10) 0;
  background: #FFFFFF;
  @include swiper-buttons();
}
.b-plans-slider__header {
  @include footerMain;
  color: #202931;
  margin-bottom: g(12);
  padding: 0 15px;
}

.b-plans-slider__slider {

}
.b-plans-slider__swiper-container {

}
.b-plans-slider__slide {
  display: flex;
  padding: 0 15px;
  @include media-breakpoint-down(sm) {
    flex-direction: column-reverse;
  }
}
.b-plans-slider__text {
  @include media-breakpoint-down(sm) {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 15px 0;
  }
}
.b-plans-slider__title {
  @include cardTitle;
  margin-bottom: g(8);
  color: #202931;
}
.b-plans-slider__desc {
  @include linkText;
  color: #202931;
  max-width: 210px;
  margin-bottom: g(12);
}
.b-plans-slider__navigation {
  @include swiper-arrow();
}
.b-plans-slider__img {

}
