.b-docs-item {
  border-bottom:1px solid #D9D9D9 ;
  padding-bottom: rem(60px);
  position: relative;
  &:after {
    position: absolute;
    content: '';
    width: 1px;
    height: 100%;
    background: #D9D9D9;
    top: 0;
    @each $bp, $container-width in $container-max-widths {
      @include media-breakpoint-only($bp) {
        left: calc((#{$container-width} / 2) - (#{$grid-gutter-width} / 2));
      }
    }

    //transform: translateX(-50%);
  }
}
.b-docs-item__row {
  align-items: end;
}
.b-docs-item__num {
  @include pointTitle();
  color: #202931;
}
.b-docs-item__title {
  @include cardTitle();
  @include margin-bottom-not-last(15px);

  color: #202931;
}
.b-docs-item__date-block {
  @include margin-bottom-not-last(rem(44px));
}
.b-docs-item__date {
  @include pointTitle();
  color: #202931;
}
.b-docs-item__bottom {
  display: flex;
  align-items: center;
}
.b-docs-item__download {
  margin-right: 20px;
}
.b-docs-item__text {
  @include footerDescription();
  color: #BAA174;
}


@include media-breakpoint-down(md) {
  .b-docs-item {
    &:after {
      display: none;
    }
  }
  .b-docs-item__hidden {
    display: none;
  }
}

@include media-breakpoint-down(sm) {
  .b-docs-item__num {
    display: flex;
    justify-content: end;
    font-size: 18px;
  }
  .b-docs-item__row {
    @include gutter-y(10px);
  }
  .b-docs-item__title {
    font-size: 20px;
    @include margin-bottom-not-last(18px);
  }
  .b-docs-item__date {
    font-size: 16px;
    @include margin-bottom-not-last(10px);
  }
  .b-docs-item__date-block {
    @include margin-bottom-not-last(18px);
  }
  .b-docs-item__text {
    font-size: 16px;
  }
  .b-docs-item {
    padding-bottom: 30px;
  }
}
