.b-visual-floor__img {
    position: relative;
    display: inline-block;
    width: 100%;
    img {
        aspect-ratio: auto;
        height: auto;
        display: block;
        width: 100%;
    }
}

.b-visual-floor__svg {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 15;
}

.b-visual-floor__contour {
    fill: #BAA174;
    stroke: #BAA174;
    stroke-width: 10px;
    opacity: 0.7;
    transition: 0.2s opacity;

    &:hover {
        opacity: 1;
    }
}

.b-visual-floor__title {
    @include footerMain;
    color: #202931;
    margin-bottom: g(12);
    padding: 0 15px;
}

.b-visual-floor__add {
    z-index: 2;
    position: absolute;
    right: 15px;
    top: 15px;
    background: #2C3A47;
    padding: g(8);
    border-radius: 10px;
    transition: 0.3s display;
    @include media-breakpoint-down(sm) {
        display: none !important;
    }
}

.b-visual-floor__add-item {
    display: flex;
    flex-direction: column;
    align-items: center;
    @include margin-bottom-not-last(g(6));
}
.b-visual-floor__add-count {
    @include cardTitle;
    font-family: $Garamond;
    color: #FFFFFF;
    margin-bottom: g(3);
    text-transform: uppercase;
    @include media-breakpoint-down(md) {
        font-size: 14px;
    }
}

.b-visual-floor__add-text {
    @include text1;
    color: #BAA174;
    line-height: 2;
    display: flex;
    flex-direction: column;
    @include media-breakpoint-down(md) {
        font-size: 12px;
    }
}



.scrollbar-external {
    height: 400px;
    overflow: auto;
    width: 600px;
}

/******************* EXTERNAL SCROLLBAR *******************/

.external-scroll_x,
.external-scroll_y {
    border: solid 1px #9999fc;
    cursor: pointer;
    display: none;
    position: absolute;
}

.external-scroll_x.scroll-scrollx_visible {
    display: block;
    height: 10px;
    left: 0;
    top: 410px;
    width: 200px;
}

.external-scroll_y.scroll-scrolly_visible {
    display: block;
    height: 200px;
    left: 610px;
    top: 0;
    width: 10px;
}

.external-scroll_x div,
.external-scroll_y div
{
    height: 100%;
    left: 0;
    top: 0;
    position: absolute;
    width: 100%;
}

.external-scroll_x .scroll-bar,
.external-scroll_y .scroll-bar {
    background: #9999fc;
    z-index: 12;
}

.external-scroll_x .scroll-bar {
    width: 100px;
}
.external-scroll_y .scroll-bar {
    height: 100px;
}
