.page-header {
  &.fixed-header {
    transform: translateY(-100%);
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    transition: .6s transform;
    z-index: 20;
    padding-right: inherit !important;
    will-change: transform;
    background: #202931;
    &.sticky {
      background: #202931;
      transform: translateY(0%);
      padding: g(8) 0;
      .page-header__logo {
        z-index: 0;
      }
    }
  }
  padding: g(10) 0;
  z-index: 1;
  width: 100%;
  background: transparent;
  @include media-breakpoint-down(lg) {
    padding: g(5) 0;
  }
}
.page-header_inner {
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 18;
  background: linear-gradient(180deg, #25282C 0%, rgba(37, 40, 44, 0) 100%);
}
.page-header__nav-list {
  display: flex;
  gap: g(6);
  &_mobile {
    flex-direction: column;
    .page-header__nav-item {
      @include text3;
      color: #FFFFFF;
    }
  }
  @include media-breakpoint-down(lg) {
    justify-content: space-between;
  }
  @include media-breakpoint-down(md) {
    display: none;
    &_mobile {
      display: flex;
      flex-direction: column;
    }
  }
  @include media-breakpoint-down(sm) {
    &_mobile {
      margin-bottom: g(14);
    }
  }
}
.page-header__nav-item {
  @include text1;
  color: #FFFFFF;
  transition: 0.3s color;
  font-size: 16px;
  @include media-breakpoint-down(lg) {
    font-size: 12px;
  }
  &:hover {
    color: #BAA174;
  }
}
.page-header__row {
  align-items: center;
}
.page-header__content {
  display: flex;
  justify-content: space-between;
  align-items: center;
  .page-header__logo {
    z-index: -1;
  }
}
.page-header__right-col {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.page-header__logo {
  margin-right: g(12);
  position: relative;
  z-index: 10;
}
.page-header__social {
  display: flex;
  gap: g(7);
  @include media-breakpoint-down(md) {
    display: none;
    &_mobile {
      display: flex;
      margin-bottom: g(10);
      .page-header__social-item {
        width: g(12);
        height: g(12);
      }
    }
  }
}
.page-header__social-item {
  display: flex;
  justify-content: center;
  align-items: center;
  border: 1px solid #BAA174;
  border-radius: 50%;
  width: g(10);
  height: g(10);
  cursor: pointer;
  fill: #BAA174;
  transition: 0.3s fill, 0.3s background;
  &:hover {
    fill: #202931;
    background: #BAA174;
  }
  svg {
    width: 50%;
    height: 50%;
  }
}
.page-header__contacts-col {
  display: flex;
  gap: g(9);
  justify-content: flex-end;
  align-items: baseline;
}
.page-header__tel {
  @include text1;
  color: #FFFFFF;
  &_mobile {
    @include text4;
  }
  @include media-breakpoint-down(lg) {
    display: none;
    &_mobile {
      display: block;
    }
  }
}
.page-header__callback {
  @include text2;
  color: #BAA174;
  cursor: pointer;
  border-bottom: 1px solid transparent;
  &:hover {
    border-bottom: 1px solid #BAA174;
  }
  @include media-breakpoint-down(lg) {
    display: none;
    &_mobile {
      display: block;
    }
  }
}
.page-header__burger-wrap {
  display: flex;
  align-items: center;
  gap: g(10);
  cursor: pointer;
  &:hover {
    .page-header__burger-text {
      color: #BAA174;
    }
    .page-header__gamburger-line1,
    .page-header__gamburger-line2,
    .page-header__gamburger-line3{
      background: #BAA174;
    }
  }
}
.page-header__burger-text {
  @include text2;
  color: #FFFFFF;
  transition: 0.3s color;
}
.page-header__gamburger {
  position: relative;
  display: flex;
  width: 17px;
  height: 10px;
  cursor: pointer;
  transition: 0.3s rotate, 0.3s display;
  .page-header__gamburger-line1,
  .page-header__gamburger-line2,
  .page-header__gamburger-line3{
    position: absolute;
    height: 1px;
    width: 100%;
    background: #FFFFFF;
    transition: 0.3s rotate, 0.3s display, 0.3s background;
  }
  .page-header__gamburger-line1 {
    top: 0;
  }
  .page-header__gamburger-line2 {
    top: 50%;
  }
  .page-header__gamburger-line3 {
    bottom: 0;
  }
  &_open {
    justify-content: center;
    align-items: center;
    .page-header__gamburger-line2 {
      top: 5px;
      rotate: 45deg;
    }
    .page-header__gamburger-line1 {
      display: none;
    }
    .page-header__gamburger-line3 {
      top: 50%;
      rotate: -45deg;
    }
  }
}
.page-header__mobile-menu {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  background: #202931;
  display: none;
  @include media-breakpoint-down(sm) {
    height: 100vh;
  }
}
.page-header__row-top {
  padding-top: g(10);
  margin-bottom: g(12);
  @include media-breakpoint-down(lg) {
    padding: g(5) 0;
  }
}
.page-header__row-bottom {
  padding-bottom: g(14);
}
.page-header__mobile-text {
  @include text1;
  color: #FFFFFF;
}
.page-header__mobile-contacts {
  display: flex;
  flex-direction: column;
  height: 100%;
}
.page-header__mobile-contacts-top {
  display: flex;
  flex-direction: column;
  gap: g(8);
}
.page-header__mobile-contacts-bottom {
  display: flex;
  gap: g(8);
  margin-top: auto;
  transition: 0.3s padding-left;
  &:hover {
    padding-left: g(8);
  }
}
