.b-our-apartments-card {

}
.b-our-apartments-card__top {
  @include margin-bottom-not-last(rem(36px));
}
.b-our-apartments-card__img {
  img {
    display: block;
    aspect-ratio: 0.94;
    object-fit: cover;
    border-radius: 2px;
  }
}
.b-our-apartments-card__bottom {

}
.b-our-apartments-card__title {
  @include cardTitle;
  color: #202931;
  @include margin-bottom-not-last(17px);
}
.b-our-apartments-card__description {
  @include cardDescription;
  color: #202931;
  @include margin-bottom-not-last(rem(30px));
  max-width: 210px;
  @include media-breakpoint-down(md) {
    max-width: 100%;
  }
}
.b-our-apartments-card__link {
  display: flex;
  align-items: center;
  transition: 0.3s padding-left;
  &:hover {
    padding-left: g(8);
  }
}
.b-our-apartments-card__arrow {
  fill: #BAA174;
  margin-right: 18px;
  svg {
    width: 38px;
    height: 21px;
  }


}
.b-our-apartments-card__link-text {
  @include linkText;
  color: #BAA174;
}

@include media-breakpoint-down(sm) {
  .b-our-apartments-card__img {
    img {
      border-radius: 5px;
    }
  }
  .b-our-apartments-card__top {
    @include margin-bottom-not-last(18px);
  }
  .b-our-apartments-card__title {
    @include margin-bottom-not-last(11px);
  }
  .b-our-apartments-card__description {
    @include margin-bottom-not-last(16px);
  }

}

