.b-our-apartments {
  @include swiper-buttons();
}
.b-our-apartments__row {
  align-items: center;
  @include margin-bottom-not-last(rem(80px));
}
.b-our-apartments__title {
  @include sectionTitle;
  color: #202931;
}
.b-our-apartments__navigation {
  @include swiper-arrow();
  justify-content: end;
}
.b-our-apartments__slider {

}
.b-our-apartments__swiper-container {

}
.b-our-apartments__mobile {
  display: none;
}
@include media-breakpoint-down(lg) {
  .b-our-apartments__row {
    @include margin-bottom-not-last(2rem);
  }
}

@include media-breakpoint-down(sm) {
  .b-our-apartments__mobile {
    display: block;
  }
  .b-our-apartments__desctop {
    display: none;
  }
  .b-our-apartments__title {
    padding-left: 40px;
    @include margin-bottom-not-last(26px);
  }
  .b-our-apartments__card {
    @include margin-bottom-not-last(44px);
  }
}

@media (max-width: 320px) {
  .b-our-apartments__title {
    padding-left: 20px;
  }
}
