
.page-footer {
  background: #1C242B;
}
.page-footer__content {
  padding-top: g(12);
  padding-bottom: g(10);
}
.page-footer__top {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: g(12);
  @include media-breakpoint-down(md) {
    flex-direction: column;
  }
}
.page-footer__nav-list {
  display: flex;
  gap: g(10);
  @include media-breakpoint-down(md) {
    order: 2;
    flex-direction: column;
  }
}
.page-footer__nav-item {
  @include text5;
  color: #959595;
  transition: 0.3s color;
  &:hover {
    color: #BAA174;
  }
  @include media-breakpoint-down(md) {
    font-size: 14px;
  }
}
.page-footer__logo {
  @include media-breakpoint-down(md) {
    order: 1;
    margin-bottom: g(10);
  }
}
.page-footer__bottom {
  display: flex;
  gap: g(12);
  @include media-breakpoint-down(md) {
    flex-direction: column;
    gap: g(10);
  }
}
.page-footer__contacts {
  @include text5;
  color: #959595;
  @include media-breakpoint-down(md) {
    text-align: center;
  }
}
.page-footer__privacy {
  @include text6;
  color: #959595;
  @include media-breakpoint-down(md) {
    text-align: center;
  }
}
