.b-slider-nav {
  display: grid;
  grid-template-columns: repeat(2, minmax(0, 1fr));
  gap: 20px;
}
.b-slider-nav__btn {
  width: rem(48px);
  height: rem(48px);
  user-select: none;
  border: 1px solid #191919;
  stroke: #191919;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  cursor: pointer;
  transition: 0.2s;
  position: relative;

  &.swiper-button-disabled {
    pointer-events: none;
  }
  svg {
    width: rem(24px);
    height: rem(24px);
    transition: 0.3s;
    transition-timing-function: linear;
    display: inline-block;
    &:nth-child(2) {
      position: absolute;
      transform: translateX(-100px);
    }
  }


}
.b-slider-nav__btn_next {
  &:hover {
    transition: 0.3s transform;
    transform: scale(1.3);
  }
}
.b-slider-nav__btn_prev {
  transform: rotate(180deg);
  &:hover {
    transition: 0.3s transform;
    transform: scale(1.3) rotate(180deg);
  }
}
.b-slider-nav__arr-cont {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
}

@include media-breakpoint-down(xs) {
  .b-slider-nav {
    gap: 10px;
  }
}

.b-slider-nav_main {
  display: flex;
  .b-slider-nav__btn {
    border: 1px solid #BAA174;
    stroke: #BAA174;
  }
}

