.b-two-img {

}
.b-two-img__content {
  display: flex;
  justify-content: space-between;
  gap: g(14);
  @include media-breakpoint-down(sm) {
    flex-direction: column;
    gap: g(12);
  }
}
.b-two-img__left {
  padding-top: rem(90px);
}
.b-two-img__left-img {
  @each $bp, $container-width in $container-max-widths {
    @include media-breakpoint-only($bp) {
      margin-left: calc((100vw - #{$container-width - $grid-gutter-width}) / 2 * -1);
    }
  }
  @include media-breakpoint-down(sm) {
    margin-left: -15px;
  }
  img {
    display: block;
  }
}
.b-two-img__right {
}
.b-two-img__right-img {
  margin-bottom: g(10);
  @include media-breakpoint-down(sm) {
    display: none;
  }
}
.b-two-img__title {
  @include sectionTitle;
  color: #202931;
  max-width: 390px;
  margin-left: -100px;
  @include margin-bottom-not-last(rem(43px));
}
.b-two-img__description {
  @include tabTitle;
  color: #202931;
  margin-left: -100px;
  max-width: 390px;
  @include media-breakpoint-down(sm) {
    margin-left: 0;
  }
}
.b-two-img {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
}

@include media-breakpoint-down(sm) {
  .b-two-img__title {
    margin-left: 0;
  }
  .b-two-img__left-img {

    max-width: 90%;
  }
  .b-two-img__left {
    padding-top: 0;
  }
}
