.b-features {
  overflow: hidden;
  @include swiper-buttons();
}
.b-features__content {
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
}
.b-features__header {
  @include sectionTitle;
  color: #FFFFFF;
  padding-top: g(14);
  padding-left: g(11);
  @include media-breakpoint-down(sm) {
    padding-left: g(12);
  }
}
.b-features__card {
  background: #202931;
  padding: g(12);
  @include media-breakpoint-down(lg) {
    padding: g(10);
  }
  @include media-breakpoint-down(sm) {
    display: none;
  }
}
.b-features__grid {
  @include grid-template-columns(2);
  gap: g(12);
}
.b-features__item {
  display: flex;
  @include media-breakpoint-down(sm) {
    flex-direction: column;
    gap: g(8);
    height: 100%;
  }
}
.b-features__num {
  @include cardTitle;
  font-family: $Garamond;
  color: #B5A782;
}
.b-features__text {
  margin-top: g(10);
  margin-bottom: g(10);
  @include media-breakpoint-down(sm) {
    margin-top: 0;
  }
}
.b-features__title {
  @include cardTitle;
  font-family: $Garamond;
  color: #FFFFFF;
  margin-bottom: g(3);
  text-transform: uppercase;
}
.b-features__desc {
  @include cardDescription;
  color: #FFFFFF;
  max-width: 250px;
}
.b-features__navigation {
  @include swiper-arrow();
  margin-top: auto;
}
.b-features__slider {
  display: none;
  @include media-breakpoint-down(sm) {
    display: block;
  }
}
.b-features__swiper-container {

}

.b-features__slide {
  margin-left: g(12);
  background: #202931;
  padding: g(12) g(10);
  height: 100%;
}

  .b-features__col {
    position: inherit;
  }
  .page__second-for-main-block {
    position: relative;
  }
  .b-features__card {
    position: absolute;
  }

  @include media-breakpoint-down(sm) {
    .b-features__slider {
      position: absolute;
    }
  }

