.b-img-text {

}
.b-img-text__row {
  align-items: center;
  justify-content: space-between;
  gap: g(10);
}
.b-img-text__col-first {
  order: 1;
}
.b-img-text__img {
  img {
    width: 100%;
    display: block;
  }
}
.b-img-text__col-second {
  order: 3;
}
.b-img-text__title {
  @include sectionTitle();
  color: #202931;

}
.b-img-text__block-title {
  @include margin-bottom-not-last(rem(36px));
}
.b-img-text__description {
  @include tabTitle();
  color: #202931;
  line-height: 1.8;
  font-size: 20px;
}
.b-img-text__col-hidden {
  order: 2;
}

.b-img-text_right {
  .b-img-text__col-second {
    order: 1;
  }
  .b-img-text__col-first {
    order: 3;
  }
}

@include media-breakpoint-down(md) {
  .b-img-text__col-first {
    order: 2;
  }
  .b-img-text__col-hidden {
    display: none;
  }
  .b-img-text__col-second {
    order: 1;
  }
  .b-img-text__row {
    @include gutter-y(30px);
  }
}

@include media-breakpoint-down(sm) {
  .b-img-text__col-first {
    order: 1;

  }
  .b-img-text__col-second {
    order: 2;
  }
  .b-img-text__row {
    @include gutter-y(35px);
  }
  .b-img-text__title {

    font-size: 48px;

    max-width: 195px;
  }
  .b-img-text__block-title {
    display: flex;
    justify-content: center;
    @include margin-bottom-not-last(26px);
  }
  .b-img-text__description {
    font-size: 16px;
    max-width: 300px;
  }
  .b-img-text__img {
    img {
      width: 80%;
    }
  }
  .b-img-text_right {
    .b-img-text__description {
      @include media-breakpoint-down(sm) {
        max-width: 100%;
        padding-top: g(10);
      }
    }

    .b-img-text__img {
      display: flex;
      justify-content: end;
      @include media-breakpoint-down(sm) {
        margin-right: -15px;
      }
    }
    .b-img-text__col-first {
      order: 1;

    }
    .b-img-text__block-title {
      justify-content: start;
    }
  }
}
.b-img-text_reverse {
  @include media-breakpoint-down(sm) {
    .b-img-text__row {
      flex-direction: column-reverse;
    }
  }
}

