.b-about {
  position: relative;
}
.b-about__top {
  @include grid-template-columns(145);
  position: absolute;
}
.b-about__left {
  @include col-span(84);
  @include row-span(3);
  padding: rem(70px) rem(136px) rem(70px);
  @each $bp, $container-width in $container-max-widths {
    @include media-breakpoint-only($bp) {
      padding-left: calc((100vw - #{$container-width - $grid-gutter-width}) / 2);
    }
  }
  background: #202931;

}
.b-about__title {
  @include footerMain();
  color: #FFFFFF;
  @include margin-bottom-not-last(rem(30px));
}
.b-about__description {
  @include descriptionBlock();
  color: #FFFFFF;
  @include margin-bottom-not-last(rem(56px));
  font-size: 20px;
}
.b-about__tabs-list {
  max-width: 450px;
  @include grid-template-columns(2);
  gap: rem(92px);
}
.b-about__tab {
  @include col-span(1);
  &:last-child {
    @include col-span(2);
  }
}
.b-about__tab-title {
  @include tabTitle();
  color: #FFFFFF;
  @include margin-bottom-not-last(8px);
}
.b-about__tab-description {
  @include cardDescription();
  color: #FFFFFF;
}
.b-about__right {
  @include col-span(61);
  padding: rem(50px) rem(80px);
  background: #E1C39D;
  @include row-span(1);
}
.b-about__white {
  @include col-span(61);
  @include row-span(1);
  height: 173px;
  background: #fff;
}
.b-about__text {
  max-width: 380px;
  text-transform: uppercase;
  @include cardTitle();
  color: #303030;
}
.b-about__bottom {

}
.b-about__img {
  img {
    width: 100%;
  }
}

@include media-breakpoint-down(lg) {
  .b-about__left {
    padding: rem(30px) rem(40px) rem(30px);
    @each $bp, $container-width in $container-max-widths {
      @include media-breakpoint-only($bp) {
        padding-left: calc((100vw - #{$container-width - $grid-gutter-width}) / 2);
      }
    }
  }
  .b-about__title {
    font-size: 36px;
    @include margin-bottom-not-last(20px);
  }
  .b-about__description {
    font-size: 16px;
    @include margin-bottom-not-last(20px);
  }
  .b-about__tabs-list {
    gap: 20px;
  }
  .b-about__tab-title {
    font-size: 16px;
    @include margin-bottom-not-last(4px);
  }
  .b-about__tab-description {
    font-size: 12px;
  }
  .b-about__white {
    display: none;
  }
  .b-about__right {
    padding: rem(30px) rem(40px);
  }
  .b-about__text {
    font-size: 24px;
  }
}
@include media-breakpoint-down(md) {
  .b-about__top {
    display: block;
    position: inherit;
  }
  .b-about__right {
    position: absolute;
    top: 177%;
    right: 0;
  }
}
@include media-breakpoint-down(sm) {
  .b-about__left {
      padding-top: 54px;
      padding-bottom: 65px;
      @each $bp, $container-width in $container-max-widths {
        @include media-breakpoint-only($bp) {
          padding-left: calc((100vw - #{$container-width - $grid-gutter-width}) / 2);
          padding-right: calc((100vw - #{$container-width - $grid-gutter-width}) / 2);
        }
      }
  }
  .b-about__title {
    font-size: 50px;
    @include margin-bottom-not-last(24px);
  }
  .b-about__description {
    font-size: 14px;
    @include margin-bottom-not-last(40px);
  }
  .b-about__tabs-list {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .b-about__tab {
    max-width: 200px;
  }
  .b-about__tab-title {
    font-size: 20px;
    @include margin-bottom-not-last(3px);
  }
  .b-about__tab-description {
    font-size: 14px;
  }
  .b-about__right {
    top: 115%;
    width: 252px;
  }
  .b-about__text {
    font-size: 18px;
  }
}

