.b-main-block {
  position: relative;
  padding: 167px 0 198px 0;
  background-repeat: no-repeat !important;
  background-size: cover !important;
}
.b-main-block__row {
  @include gutter-y(35px);
}
.b-main-block__back {
  display: flex;
  align-items: center;
}
.b-main-block__arrow {
  margin-right: 18px;
  display: flex;
  fill: #BAA174;
  svg {
    width: 15px;
    height: 17px;
  }

}
.b-main-block__back-text {
  @include pointTitle();
  color: #BAA174;
}
.b-main-block__name {
  text-align: center;
  @include pointTitle();
  color: #FFFFFF;
}
.b-main-block__title {
  @include footerMain();
  color: #FFFFFF;
  @include margin-bottom-not-last(rem(57px));
  text-align: center;
}
.b-main-block__btn {
  text-align: center;
}
@include media-breakpoint-down(sm) {
  .b-main-block {
    padding: 102px 0 168px 0;
  }
  .b-main-block__col-hidden {
    display: none;
  }
  .b-main-block__name {
    font-size: 16px;
  }
  .b-main-block__title {
    font-size: 35px;
    @include margin-bottom-not-last(37px);
  }
  .b-main-block__row {
    @include gutter-y(30px);
  }
}
